<template>
  <div class="publicity">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-form :model="form" ref="form" label-width="120px" class="form" :rules="rules">
        <el-form-item label="封面" prop="bannerImage">
          <span class="sign">1125*450</span>
          <img class="upload-img" v-show="form.bannerImage" :src="form.bannerImage + '?imageMogr2/crop/375x150'"
               alt="图片加载失败">
          <img class="remove-img" v-show="form.bannerImage" @click="removeCover()" src="../../../assets/img/资源27@2x.png"
               alt="">
          <label for="cover">
            <div class="cover-img">
              <img class="loading" v-show="!form.bannerImage" src="../../../assets/img/资源28@2x.png" alt="">
            </div>
            <input accept='image/*' @change="uploadCover($event)" ref="uploadCover" id="cover" type="file" hidden>
          </label>

        </el-form-item>
        <el-form-item label="标题：" prop="title">
          <el-input v-model="form.title" style="width: 51%">
          </el-input>
        </el-form-item>
        <el-form-item label="正文内容：" prop="content">
          <wang-editor :content.sync="form.content" v-if="form.content || !id"></wang-editor>
        </el-form-item>
        <el-form-item label="附件：">
          <div class="upload_view">
            <div class="upload_view_list">
            </div>
            <div class="upload_view_button">
              <uploader
                      ref="uploadZone"
                      :options="uploadOptions"
                      :file-status-text="fileStatusText"
                      class="upload_view_btn">
                <uploader-unsupport></uploader-unsupport>
                <uploader-btn :attrs="attrs">上传文件</uploader-btn>
                <div v-for="(item,key) in form.attachmentList" class="item">
                  <span>文件{{key + 1}}：<a :href="item.fileUrl">{{item.fileName}}</a></span>
                  <i @click="removeEnd(key)" class="el-icon-close"></i>
                </div>
                <uploader-list></uploader-list>
              </uploader>
            </div>
          </div>
        </el-form-item>
      </el-form>
      <div class="btns">
        <div @click="doSubmit()" class="btn-submit">确定</div>
        <div @click="$router.go(-1)" class="btn-clear" style="margin-left: 30px">返回</div>
      </div>
    </main-part>
  </div>
</template>

<script>
  // 复用组织机构中的组件
  import item from '../../school/user/Item'

  export default {
    components: {
      item
    },
    data () {
      // 验证正文内容
      const checkContent = (rule, value, callback) => {
        if (!value || value === '<p><br></p>') {
          callback(new Error('请输入正文内容'))
        } else {
          callback()
        }
      }
      let vm = this
      return {
        id: vm.$route.query.id,
        teachGroupId: vm.$route.query.teachGroupId,
        tabPick: vm.$route.query.tabPick,
        announcementDetail: [],
        nav: [{name: '教师风采', path: 'go(-1)'}, {name: '发布', path: ''}],
        text: '发布成功',
        form: {
          title: null,
          content: null,
          attachmentList: [],
          teachGroupId: null,
          bannerImage: null
        },
        // 附件列表
        attachmentList: [],
        attrs: {
          accept: '*'
        },
        rules: {
          title: {required: true, message: '请输入标题', trigger: 'blur'},
          content: {required: true, validator: checkContent, trigger: 'change'}
        },
        uploadOptions: {
          target: `${window.location.origin}${this.$conf.API_PATH}/uploadFileTrunk/trunkUpload.do`,
          testChunks: false,
          chunkSize: 2 * 1024 * 1024,
          singleFile: false,
          parseTimeRemaining: (timeRemaining, parsedTimeRemaining) => {
            return parsedTimeRemaining
              .replace(/\syears?/, '年')
              .replace(/\days?/, '天')
              .replace(/\shours?/, '小时')
              .replace(/\sminutes?/, '分钟')
              .replace(/\sseconds?/, '秒')
          }
        },
        // 修改分片上传文字
        fileStatusText: {
          success: '上传成功',
          error: '上传错误',
          uploading: '正在上传',
          paused: '已暂停',
          waiting: '等待中'
        },
      }
    },
    watch: {
      'form.content': function () {
        if(this.id){
          setTimeout(() => {
            this.$refs.form.validate()
          }, 1)
        }
      },
      'form.title': function () {
        if (this.id){
          setTimeout(() => {
            this.$refs.form.validate()
          }, 1)
        }
      }
    },
    mounted () {
      //获取ID
      this.form.teachGroupId =Number(this.teachGroupId)
      // 上传
      this.$nextTick(() => {
        let uploadZone = this.$refs.uploadZone
        if (uploadZone) {
          const uploader = uploadZone.uploader
          uploader.on('fileSuccess', async (rootFile, file, message) => {
            let res = await this.$ajax({
              url: `/uploadFileTrunk/mergeChunks.do`,
              query: {
                fileMd5: file.uniqueIdentifier,
                fileName: file.name
              }
            })
            this.attachmentList.push({
              fileName: file.name,
              fileUrl: res
            })
          })
          uploader.on('fileRemoved', async (file) => {
            this.attachmentList.some((e, i) => {
              if (e.name === file.name) {
                this.attachmentList.splice(i, 1)
                return true
              }
            })
          })
        }
      })
      if (this.id) {
        this.getDetail()
      }
    },
    methods: {
      // 删除已经上传的视频
      removeEnd (key) {
        this.form.attachmentList.splice(key, 1)
        console.log(this.form.attachmentList)
      },
      // 获取详情
      async getDetail () {
        // console.log(this.id)
        this.announcementDetail = await this.$ajax({
          url: '/notice/getNotice.do',
          el: '.form',
          query: {
            id: this.id
          }
        })
        this.form.title = this.announcementDetail.title
        this.form.content = this.announcementDetail.content
        this.form.bannerImage = this.announcementDetail.bannerImage
        // 附件
        this.form.attachmentList = this.announcementDetail.attachmentList
      },
      // 上传封面图
      async uploadCover (e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        if (file.size > 1024 * 1024 * 10) {
          this.$message.warning('封面图不能超过10M')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/oss/upload.do',
          query: formData,
          el: '.cover-img',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.form.bannerImage = res
        this.$refs.uploadCover.value = null
        console.log(this.form.bannerImage)
      },
      // 移除封面图
      removeCover () {
        this.form.bannerImage = null
      },
      // 提交
      async doSubmit () {
        await this.$refs.form.validate()
        // 处理文件
        if (this.attachmentList.length) {
          this.form.attachmentList = this.form.attachmentList.concat(this.attachmentList)
          // console.log(this.attachmentList)
        }
        let url = '/notice/addTeachGroup.do'
        if (this.id) {
          this.text = '修改成功'
          url = '/notice/updateTeachGroup.do'
          this.form.id = this.id
        }
        await this.$ajax({
          url,
          el: '.form',
          query: this.form,
        })
        this.$remind(this.text)
        this.$router.push('/research-orsay/teachingTeam')
      },
    }
  }
</script>
